body {
  color: #333;
  font-family: "GraphikLCG", "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  background: #fff;
}

.inner {
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1600px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.block {
  display: flex;
}

.title-xl {
  font-size: 120px;
  font-weight: 700;
  line-height: 1.084;
  letter-spacing: -4px;
}

@media only screen and (max-width: 767px) {
  .title-xl {
    font-size: 60px;
    line-height: 1.216;
    letter-spacing: -2px;
  }
}

.title-lg {
  font-size: 80px;
  font-weight: 700;
  line-height: 0.975;
  letter-spacing: -1px;
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 40px;
    line-height: 1.125;
  }
}

.title-md {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.1875;
}

@media only screen and (max-width: 767px) {
  .title-md {
    font-size: 28px;
    line-height: 1.214;
  }
}

.article {
  color: #333;
}

.article .title-xl {
  padding-bottom: 60px;
}

@media only screen and (max-width: 767px) {
  .article .title-xl {
    padding-bottom: 28px;
  }
}

.article .title-md {
  padding-top: 60px;
  padding-bottom: 20px;
}

@media only screen and (max-width: 767px) {
  .article .title-md {
    padding-top: 26px;
    padding-bottom: 20px;
  }
}

.article p,
.article li {
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.555;
}

@media only screen and (max-width: 767px) {
  .article p,
  .article li {
    padding-top: 14px;
    padding-bottom: 14px;
  }
}

.article ol {
  list-style-type: decimal;
  list-style-position: outside;
  padding-left: 20px;
}

.article ol li {
  padding-left: 10px;
}

.article ul li {
  position: relative;
  padding-left: 30px;
}

@media only screen and (max-width: 767px) {
  .article ul li {
    padding-left: 40px;
  }
}

.article ul li:before {
  content: '';
  position: absolute;
  top: 29px;
  left: 0;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background: #333;
}

@media only screen and (max-width: 767px) {
  .article ul li:before {
    top: 23px;
    left: 7px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 40px;
  color: #fff;
}

.logo {
  float: left;
}

.nav {
  float: left;
  margin-left: 167px;
  font-size: 16px;
  font-weight: 700;
}

.nav__item {
  vertical-align: top;
  display: inline-block;
}

.nav__item:not(:first-child) {
  margin-left: 24px;
}

.nav__link {
  opacity: .6;
  color: #fff;
  transition: opacity .3s ease-in-out;
}

.no-touch .nav__link:hover {
  opacity: 1;
}

.lang {
  position: relative;
  float: right;
  padding-right: 42px;
  min-width: 42px;
}

.lang:before {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 3.5px 0 3.5px;
  border-color: #fff transparent transparent transparent;
}

.lang__list {
  position: absolute;
  top: 100%;
  width: 42px;
  left: 0;
  display: none;
}

.lang__item {
  display: block;
}

.topic {
  height: 100vh;
  background-image: linear-gradient(138deg, #2D3353 9%, #3E3E88 70%);
}

.privacy {
  position: relative;
  padding: 204px 60px 100px 20px;
  margin: 0 auto;
  max-width: 880px;
}

.privacy:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 767px) {
  .privacy {
    padding: 118px 20px 20px;
  }
}

.contact {
  padding-top: 83px;
  height: 100vh;
  color: #000;
}

@media only screen and (max-width: 767px) {
  .contact {
    padding-top: 73px;
    padding-bottom: 37px;
    height: auto;
  }
}

.contact__map {
  position: relative;
  overflow: hidden;
  margin-left: 420px;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .contact__map {
    height: 316px;
    margin-bottom: -63px;
  }
}

.contact__map-inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.contact__title, .contact__mail {
  white-space: nowrap;
}

.contact__title {
  position: absolute;
  z-index: 2;
  top: 185px;
  left: 40px;
  padding: 7px 0;
}

@media only screen and (max-width: 767px) {
  .contact__title {
    position: relative;
    top: auto;
    left: auto;
    padding: 36px 18px 31px;
  }
}

.contact__mail {
  display: block;
  margin-top: 40px;
  color: #0171BB;
  font-size: 32px;
  font-weight: 700;
  line-height: 36px;
}

@media only screen and (max-width: 767px) {
  .contact__mail {
    margin-top: 16px;
    font-size: 20px;
    line-height: 36px;
  }
}

.contact__address {
  font-size: 24px;
  line-height: 28px;
}

@media only screen and (max-width: 767px) {
  .contact__address {
    font-size: 20px;
  }
}

.contact__info {
  position: absolute;
  z-index: 2;
  left: 40px;
  bottom: 127px;
}

@media only screen and (max-width: 767px) {
  .contact__info {
    position: relative;
    left: auto;
    bottom: auto;
    padding: 20px 20px 22px;
    background: rgba(255, 255, 255, 0.7);
  }
}

.contact__copy {
  left: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 17px;
}

@media only screen and (max-width: 767px) {
  .contact__copy {
    left: auto;
    padding: 0 17px;
    font-size: 12px;
    line-height: 14px;
  }
}

.contact__nav {
  right: 40px;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .contact__nav {
    display: none;
  }
}

.contact__nav a {
  display: inline-block;
  vertical-align: top;
  padding-top: 3px;
  color: #000;
  font-size: 12px;
  line-height: 14px;
}

.contact__nav a:not(:last-child) {
  margin-right: 16px;
}

.contact__copy, .contact__nav {
  position: absolute;
  z-index: 2;
  bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .contact__copy, .contact__nav {
    position: relative;
    bottom: auto;
  }
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 17px 48px;
  border-radius: 4px;
  color: #000;
  background: #fff;
  font-family: "GraphikLCG", "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  text-decoration: none;
}

@media only screen and (max-width: 767px) {
  .btn {
    padding: 12px 32px;
    border-radius: 3px;
    font-size: 14px;
    line-height: 17px;
  }
}

.footer {
  color: #fff;
  background: #2A487F;
}

.footer__inner {
  position: relative;
  padding: 90px 20px 20px;
  margin: 0 auto;
  max-width: 880px;
}

.footer__inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 767px) {
  .footer__inner {
    padding-top: 45px;
  }
}

.footer__top {
  padding-bottom: 40px;
  min-height: 451px;
}

@media only screen and (max-width: 767px) {
  .footer__top {
    padding-bottom: 90px;
    min-height: auto;
  }
}

.footer__title {
  margin-bottom: 56px;
}

@media only screen and (max-width: 767px) {
  .footer__title {
    margin-bottom: 37px;
  }
}

.footer__description {
  padding-right: 24px;
  opacity: 0.8;
  font-size: 12px;
  line-height: 1.5;
}

@media only screen and (max-width: 767px) {
  .footer__description {
    padding: 0;
    font-size: 9px;
    line-height: 13px;
  }
}

.footer__container {
  padding: 20px 40px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  font-size: 0;
}

.footer__container:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1023px) {
  .footer__container {
    padding: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .footer__container {
    padding: 15px 20px;
  }
}

.footer__link, .footer__address, .footer__copy {
  display: inline-block;
  vertical-align: top;
}

.footer__link:not(:last-child), .footer__address:not(:last-child), .footer__copy:not(:last-child) {
  margin-right: 16px;
}

@media only screen and (max-width: 767px) {
  .footer__link:not(:last-child), .footer__address:not(:last-child), .footer__copy:not(:last-child) {
    margin-right: 0;
  }
}

.footer__link, .footer__address {
  color: #fff;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

@media only screen and (max-width: 767px) {
  .footer__link, .footer__address {
    font-size: 9px;
    line-height: 2;
  }
}

.footer__copy {
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
}

@media only screen and (max-width: 767px) {
  .footer__copy {
    padding: 1px 0;
    font-size: 12px;
  }
}

@media only screen and (max-width: 767px) {
  .footer__address, .footer__mail {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .footer__link {
    opacity: 0.8;
    transition: opacity 0.25s;
  }
  .no-touch .footer__link:hover {
    opacity: 1;
  }
}

@media only screen and (max-width: 767px) {
  .footer__info, .footer__nav {
    width: 50%;
  }
}

.footer__info {
  float: left;
}

.footer__nav {
  float: right;
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .footer__nav a {
    display: block;
  }
}
