.footer {
	color: $white;
	background: $blue;
	&__inner {
		position: relative;
		padding: 90px 20px 20px;
		margin: 0 auto;
		max-width: 880px;
		@include clr;
		@include sm {
			padding-top: 45px; } }
	&__top {
		padding-bottom: 40px;
		min-height: 451px;
		@include sm {
			padding-bottom: 90px;
			min-height: auto; } }
	&__title {
		margin-bottom: 56px;
		@include sm {
			margin-bottom: 37px; } }
	&__description {
		padding-right: 24px;
		opacity: 0.8;
		@include f(12px, null, 1.5);
		@include sm {
			padding: 0;
			@include f(9px, null, 13px); } }
	&__container {
		padding: 20px 40px;
		border-top: 1px solid rgba($white, 0.2);
		font-size: 0;
		@include clr;
		@include md {
			padding: 20px; }
		@include sm {
			padding: 15px 20px; } }
	&__link,
	&__address,
	&__copy {
		display: inline-block;
		vertical-align: top;
		&:not(:last-child) {
			margin-right: 16px;
			@include sm {
				margin-right: 0; } } }
	&__link,
	&__address {
		color: $white;
		@include f(12px, 400, 14px);
		@include sm {
			@include f(9px, null, 2); } }
	&__copy {
		@include f(14px, 700, 14px);
		@include sm {
			padding: 1px 0;
			font-size: 12px; } }
	&__address,
	&__mail {
		@include sm {
			display: none; } }
	&__link {
		@include sm {
			opacity: 0.8;
			transition: opacity 0.25s;
			@include hover {
				opacity: 1; } } }
	&__info,
	&__nav {
		@include sm {
			width: 50%; } }
	&__info {
		float: left; }
	&__nav {
		float: right;
		text-align: right;
		a {
			@include sm {
				display: block; } } } }
